import React from 'react'
import Wrapper from '../subcomponents/wrapper/Wrapper'
import { ReactComponent as Item1 } from '../../svg/advantages/item1.svg'
import { ReactComponent as Item2 } from '../../svg/advantages/item2.svg'
import { ReactComponent as Item3 } from '../../svg/advantages/item3.svg'
import { ReactComponent as Item4 } from '../../svg/advantages/item4.svg'
import { ReactComponent as Item5 } from '../../svg/advantages/item5.svg'
import { ReactComponent as Item6 } from '../../svg/advantages/item6.svg'

import './Advantages.css'

const Advantages = (props) => {
  return (
    <div className='advantages'>
      <Wrapper medium={true}>
        <div className="advantages__title"> </div>
        <div className="advantages__container">
          <div className="advantages__item">
            <div className="advantages__circle"><Item1 /></div>
            <div className="advantages__item-title">  </div>
          </div>
          <div className="advantages__item">
            <div className="advantages__circle"><Item2 /></div>
            <div className="advantages__item-title">    </div>
          </div>
          <div className="advantages__item">
            <div className="advantages__circle"><Item3 /></div>
            <div className="advantages__item-title">  </div>
          </div>
          <div className="advantages__item">
            <div className="advantages__circle"><Item4 /></div>
            <div className="advantages__item-title">   </div>
          </div>
          <div className="advantages__item">
            <div className="advantages__circle"><Item5 /></div>
            <div className="advantages__item-title">    </div>
          </div>
          <div className="advantages__item">
            <div className="advantages__circle"><Item6 /></div>
            <div className="advantages__item-title">  </div>
          </div>
        </div>
      </Wrapper>
    </div>
  )
}

export default Advantages;
